<template>
<!-- 虽然设置了scoped属性能够只影响当前组件, 但是每个组件建议养成给他们的跟标签设置一个专属的class样式类名 -->
    <div class="login-container">
      <van-nav-bar title="登录" class="page-nav">
        <template #left>
          <van-icon name="cross" size="18" @click="$router.back()" />
        </template>
      </van-nav-bar>

      <van-form @submit="onSubmit" ref="loginForm">
        <!-- 输入框 -->
        <van-field
          v-model="user.mobile"
          name="mobile"
          label="手机号"
          placeholder="手机号"
          :rules="userFormRules.mobile"
        >
          <template #left-icon>  <!-- v-slot => #-->
            <i class="toutiao toutiao-shouji"></i>
          </template>
        </van-field>

        <van-field
          v-model="user.code"
          type="password"
          name="code"
          label="验证码"
          placeholder="验证码"
          :rules="userFormRules.code"
        >
          <!-- slot属性时老版vue插槽写法， 这种slot写法在慢慢淘汰, 虽然没有废弃但是快了 -->
          <!-- 如果只作为一个标签, 且不需要用导作用域插槽的内容时，可以减少代码量 -->
          <!-- <i slot="left-icon" class="toutiao toutiao-yanzhengma"></i> -->
          <template #left-icon>  <!-- v-slot => # -->
            <i class="toutiao toutiao-yanzhengma"></i>
          </template>

          <!-- 验证码按钮 -->
          <template #button>  <!-- v-slot => #-->
            <!-- 倒计时组件 -->
            <van-count-down
              v-if="isCountDownShow"
              :time="1000 * 5"
              format="ss 秒"
              @finish="isCountDownShow = false"
            />
            <van-button
              v-else
              type="default"
              size="small"
              native-type="button"
              @click="onSendSms()"
              round
            >
              发送验证码
            </van-button>
          </template>

        </van-field>
        <!-- 提交按钮 -->
        <div style="margin: 16px;" class="login-btn-wrap">
          <van-button block type="info" native-type="submit" class="login-btn" >登录</van-button>
        </div>
    </van-form>
    </div>
</template>

<script>
/*
  分析设计稿：
    01 发现登录页面路由为 login => 进入项目时会自动重定向(redirect)到login
    02 login登录功能, 需要写在新建的login.vue组件中 , 通过router-view组件进行切换
*/

// getSmsCodeAPI
import { getSmsCodeAPI, loginAPI } from '../../api/index.js'

export default {
  name: 'LoginIndex',

  data () {
    return {
      user: {
        mobile: '',
        code: ''
      },
      isCountDownShow: false, // 是否显示倒计时组件
      // 设置手机号验证码规则验证
      userFormRules: {
        // 手机号 验证规则数组
        // 第一条：必填
        // 第二条：手机号基本结构11位 以1开头[3479]后面9位任意数
        mobile: [
          { required: true, message: '请填写手机号' },
          { pattern: /^1[3|5|7|8]\d{9}$/, message: '手机格式错误' }
        ],
        // 验证码规则
        code: [
          // 第一条：必填
        // 第二条：验证码基本结构6位任意数
          { required: true, message: '请填写手机号' },
          { pattern: /^\d{6}$/, message: '验证码格式错误' }
        ]
      }
    }
  },

  methods: {
    async onSubmit () {
      // 请求登录接口前, 当点击登录后让用户等待
      // $toast 是vant自带的提示弹框, 可以直接调用
      // 将toast加载提示设置为持续提示, 在请求接口响应数据后主动关闭
      // 关闭加载弹出层的位置有两个, 请求成功与失败都需要单独设置关闭方式
      const toast = this.$toast.loading({
        duration: 0, // duration 属性设置为0时, 提示状态为持续提示, 需要 .clear() 来主动关闭
        message: '加载中',
        forbidClick: true // 是否禁止背景点击
      })
      // console.log('submit', this.user)
      try {
        const res = await loginAPI({
          mobile: this.user.mobile,
          code: this.user.code
        })
        console.log('登录成功', res)

        // 登录成功后把返回的token存起来
        // this.$store.commit(vuex中mutations里定义的方法名, 传递的参数) 方法去调用mutations里定义的方法
        this.$store.commit('setUser', res.data.data)

        // toast.clear()  清除弹框
        // 当提示sucess(成功)/fail(失败) 时，会自动清除后边其他的toast提示
        this.$toast.success('登录成功') // 接口请求成功
        this.$router.back()
      } catch (error) {
        toast.clear() // 清除上面成功的弹框
        if (error.response.status === 400) {
          console.log('手机号或者验证码错误', error)
          this.$toast.fail('手机号或者验证码错误') // 接口请求失败
        } else {
          console.log('未知错误, 稍后再试', error)
          this.$toast.fail('未知错误, 稍后再试') // 接口请求失败
        }
      }
    },

    // 获取验证码
    async onSendSms () {
      // 01 验证手机号  validate 支持传入 name 来验证单个或部分表单项
      try {
        await this.$refs.loginForm.validate('mobile')

        // 02 验证通过时, 显示倒计时
        this.isCountDownShow = true
      } catch (error) {
        console.log('手机号或者验证码错误', error)
      }

      // 请求验证码接口
      try {
        await getSmsCodeAPI(this.user.mobile)
        this.$toast('验证码发送成功')
      } catch (error) {
        // 验证码发送失败时直接关闭倒计时
        this.isCountDownShow = false

        if (error.response.status === 429) {
          this.$toast.fail('发送过于频繁, 请稍后重试')
        } else {
          this.$toast.fail('发送失败, 请重试')
        }
      }
    }
  }
}

</script>

<style scoped lang="less">
// 只要是同一个项目, 那么他一般就有一个统一的色调主题, 这个主题色调就可能用在顶部导航、底部导航、各种按钮等地方；因此最好把这种样式写在全局样式表中

.login-container{
  .toutiao {
    font-size: 32px;
  }
}

.login-container {
  .login-btn-wrap {
    padding: 53px 33px;

    .login-btn {
      background-color: #6db4fb;
      border: none;
    }
  }
}

.page-nav {
  .van-icon {
    color: #fff;
  }
}
</style>
